
.pagination1 li{
  float: left;
  list-style: none;
  margin-left: 4px;
}
.pagination1 li a{
  padding-left: 8px;
  padding-right: 8px;

  font-size: 16px;
  color: #999999;
  cursor: pointer;
}
.pagination1 .active {
  background-color: #0B357C;
  color: #ffffff;
  border-radius: 4px;
}
.pagination1 .active a {
  color: #fff;
}




