




















































































































































































































































/*返回顶部*/
#btn_top {
  position: fixed;
  width: 50px;
  height: 50px;
  bottom: 10px;
  right: 10px;
  display: none;
  z-index: 9999;
  background-color: rgba(255,255,255,0.3);
  border: none;
}
/*##########页脚##########*/
/*网站地图*/
#music1{
  display: none;
}
.web-footer{
  padding-top: 40px;
  width:100%;
  background-color: #3A3A3A;
  -webkit-box-shadow: 0 0 5px #000000;
          box-shadow: 0 0 5px #000000;
}
.map-footer{
  border-bottom: 1px solid white;
  padding-bottom: 6px;
}
.map-footer dt{
  text-align: left;
  padding:3px;
  font-size: 16px;
  color:#e6e6e6;
}
.map-footer dd{
  padding:3px;
  font-size: 14px;
  color: #ccc;
  text-align: left;
}
.map-footer p{
  color:#fff;
  font-size:16px;
  text-align:center;
}
.map_tel{
  margin-top: 30px;
  margin-bottom: 20px;
}
.map_weixin img{
  width: 150px;
  height: 150px;
  margin-bottom: 15px;
}
.map_link{
  text-align: left;
}
.map_link span{
  color: #ffffff;
  text-align: left;
}
.map_link a{
  margin-right: 10px;
}
.map-footer a{
  color:#A6A6A6;
  font-size:13px;
  text-decoration:none;
}
.map-footer a:hover{
  color:#fff;
  text-decoration:none;
}

/*底部版权*/
#patent-footer{
  text-align:left;
}
#patent-footer div{
  margin-top:15px;
  color:#8d8d8d;
  font-size:13px;
}
#patent-footer p{
  display: inline-block;
}
#patent-footer a{
  color: #666;
}

/*固定电话和qq*/
.tel-qq .qq_iframe{
  text-decoration:none;
}
.tel-qq{
  width: 71px;
  position: fixed;
  right: 0px;
  top: 50%;
  margin-top: -146px;
  z-index: 122;
}
.box0{
  width: 73px;
  height: 73px;
  border: 1px solid #032e84;
  text-align: center;
}
.box-qq{
  height: 74px;
  padding-top:8px;
  background: #032e84;
}
.icon-qq{
  width: 40px;
  height: 40px;
  display: block;
  margin-right: auto;
  margin-left: auto;
  background-image: url("../images/qq1.png");
  background-size: 40px;
}
.box0 .icon-qqzi{
  line-height: 27px;
  color: white;
}
.box-tel{
  border-top: 0;
  border-bottom: 0;
  padding-top:8px;
  background: white;
  cursor: pointer;
}
.icon-tel{
  width: 40px;
  height: 40px;
  display: block;
  margin-right: auto;
  margin-left: auto;
  background-image: url("../images/phone1.png");
}
.bigtel{
  width: 150px;
  height: 45px;
  position: absolute;
  background-color: #fff;
  right: -100px;
  top: 86px;
  padding: 10px;
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ccc;
  font-size: 16px;
  color: orangered;
  font-weight:600;
  overflow: hidden;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  z-index: -1;
}
.box-tel:hover .bigtel{
  right: 71px;
}
.box0 .icon-telzi{
  line-height: 29px;
  color: #032e84;
}
.box-msg{
  border-bottom: 0;
  padding-top:8px;
  background: white;
  cursor: pointer;
}
.icon-msg{
  width: 40px;
  height: 40px;
  display: block;
  margin-right: auto;
  margin-left: auto;
  background-image: url("../images/liuyan.png");
}
.icon-msg1{
  width: 40px;
  height: 40px;
  display: block;
  margin-right: auto;
  margin-left: auto;
  background-image: url("../images/yinyue.png");
  background-size: 40px;
}
.box-ma{
  padding-top:7px;
  background: white;
  position: relative;
  cursor: pointer;
}
.box-ma:hover .bigma{
  display: block;
}
.icon-ma{
  width: 40px;
  height: 39px;
  display: block;
  margin-right: auto;
  margin-left: auto;
  background-image: url("../images/erweima.png");
}
.box0 .icon-mazi{
  line-height: 27px;
  color: #032e84;
}
.bigma{
  width: 180px;
  position: absolute;
  background-color: #fff;
  right: 72px;
  top: 0;
  padding: 12px;
  display: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ccc;
}
.yzm img{
  width: 200px;
  height: 50px;
}
.yzm input{
  width: 200px;
  height: 50px;
  margin-right: 20px;
  display: inline-block;
}
/*留言*/
.liuyan .modal-dialog{
  width: 800px;
}
.liuyan .modal-title{
  font-size: 20px;
}
.liuyan .modal-body{
  padding-top: 40px;
  padding-left: 65px;
  padding-right: 65px;
}
.liuyan .control-label2{
  float: left;
  width: 20%;
  line-height: 40px;
  font-size: 24px;
  font-weight: 400;
}
.liuyan .liuyan_text{
  min-height: 200px;
}
/*响应式代码*/
/*小屏情况下*/
@media (max-width: 768px){
.tel-qq{
    display: none;
}
.map{
    display: none;
}
.map-footer .map_tel{
    margin-top: 0;
}
#patent-footer div{
    text-align: center;
    padding-bottom: 10px;
}
#patent-footer p{
    display: block;
}
}




