

























































































































































/*表单样式*/
.apply{
  background-color: #EEEEEE;
}
.sq_title{
  font-size: 24px;
  color: #000;
  font-family: "黑体";
  text-align: center;
  border-bottom: 1px solid #CCCCCC;
  line-height: 80px;
}
.sq_box{
  padding-top: 50px;
}
.form-group{
  overflow: hidden;
  margin-bottom: 40px;
}
.control-label{
  float: left;
  width: 150px;
  line-height: 40px;
  margin-right: 40px;
  font-size: 24px;
  font-weight: 400;
}
.control-label i{
  color: red;
  display: inline-block;
  width: 20px;
  line-height: 40px;
  text-align: left;
}
.form-control{
  height: 40px;
  float: left;
  width: 70%;
  font-size: 18px;
}
.form-group .form-control option{
  height: 37px;
}
.sq-btn{
  width: 300px;
  height: 50px;
  background-color: #0B357C;
  color: #ffffff;
  border-radius: 8px;
  font-size: 26px;
  margin-top: 20px;
  border: none;
  margin-bottom: 40px;
}
.yzm img{
  width: 200px;
  height: 50px;
}
.yzm input{
  width: 200px;
  height: 50px;
  margin-right: 20px;
}
/*小屏情况下*/
@media (max-width: 768px) {
.sq-btn{
    width: 200px;
    height: 50px;
    border-radius: 5px;
    font-size: 22px;
}
}



