
.list{
  background-color: #EDEDED;
}
.main{
  padding-top: 60px;
  padding-bottom: 40px;
}
.banner{
  width: 100%;
}
.banner img{
  width: 100%;
}
.banner-small{
  display: none;
}
.menu{
  width: 100%;
  padding-left: 22px;
  padding-right: 22px;
  border-bottom: 1px solid #cccccc;
}
.list .menu .active{
  color: #0C347A;
  border-bottom: 4px solid #D51618;
}
.news{
  padding: 30px 0 20px 20px;
}
.list .menu div{
  font-size: 18px;
  color: #666666;
  display: inline-block;
  margin-left: 18px;
  margin-right: 18px;
  padding-bottom: 14px;
  cursor: pointer;
}
.list_group{
  width: 100%;
  margin-bottom: 40px;
}
.list_img{
  float: left;
  max-width: 234px;
  margin-right: 18px;
}
.list_img img{
  width: 234px;
  height: 166px;
}
.list_mes{
  float: left;
  width: 478px;
}
.mes_title{
  margin-top: 3px;
  font-size: 18px;
}
.mes_title a{
  color: #333333;
}
.mes_content1{
  padding-top: 3px;
  padding-bottom: 12px;
  font-size: 14px;
  color: #808080;
  overflow: hidden;
}
.mes_keyword{
  float: left;
  height: 23px;
}
.mes_keyword span{
  text-align: center;
  line-height: 19px;
  display: inline-block;
  color: #0B357C;
  background-color: #ffffff;
  font-size: 14px;
  padding: 0 7px;
  height: 20px;
  border: 1px solid #0B357C;
  border-radius: 5px;
  margin-right: 20px;
}
.mes_time{
  color: #808080;
  font-size: 14px;
  line-height: 23px;
}
.heigh{
  min-height: 137px;
}
.mes_content1{
  max-height: 105px;
}

/*内容右部分*/
.article{
  background-color: #ffffff;
  max-height: 350px;
  width: 100%;
  margin-bottom: 20px;
  overflow: hidden;
}
.article-title{
  line-height: 40px;
  width: 100%;
  border-bottom: 1px solid #E8E8E8;
  padding-left: 10px;
  padding-right: 10px;
}
.article-title span:nth-child(1){
  font-size: 18px;
  color: #333333;
  font-family: '黑体';
  font-weight: 700;
}
.article-title span:nth-child(2){
  font-family: '黑体';
  font-size: 15px;
  float: right;
  color: #999999;
  cursor: pointer;
}
.article-title span:nth-child(2) i{
  display: inline-block;
  width: 15px;
  height: 15px;
  background: url('../../images/gengduo.jpg') no-repeat;
  vertical-align: middle;
  margin-left: 2px;
}
.article-content{
  padding-top: 15px;
  padding-bottom: 8px;
  padding: 15px 15px 8px 38px;
}
.article-content li{
  padding-top: 6px;
  padding-bottom: 7px;
  color: #0A367C;
  font-size: 20px;
}
.article-content li a{
  vertical-align: middle;
  display: inline-block;
  width: 100%;
  color: #999999;
  font-size: 16px;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}
.yema{
  margin-top: 20px;
  margin-bottom: 20px;
}
/*响应式代码*/
/*小屏情况下*/
@media (max-width: 1200px){
.list_mes{
    width: 340px;
}
}
@media (max-width: 992px){
.list_mes{
    width: 445px;
}
}
@media (max-width: 768px) {
.main{
    padding-top: 20px;
    padding-bottom: 20px;
}
.list .menu div{
    margin-left: 8px;
    margin-right: 8px;
}
.list_group{
    border-bottom: 1px solid #cccccc;
    padding-bottom: 5px;
}
.list .banner-big{
    display: none;
}
.list .banner-small{
    display: block;
}
.news{
    padding-left: 18px;
    padding-right: 18px;
}
.list_mes{
    width: 100%;
}
.list_img{
    margin-bottom: 10px;
    margin-right: 0px;
}
.mes_content2{
    margin-top: 5px;
}
.yema{
    margin-top: 0px;
}
.yema ul{
    float: left;
    padding-left: 0;
}
}
@media (max-width: 360px){
.list_img img{
    width: 100%;
}
}

/*大屏情况下*/
@media (min-width: 992px){
.main-right{
    padding-left: 30px;
    padding-right: 0;
}
}
@media (min-width: 768px) {
.mes_title{
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}
.main-left{
    padding-right: 30px;
}
.yema ul{
    float: right;
}
}




