
.articles-title{
  text-align: center;
  font-size: 24px;
  margin-top: 40px;
  margin-bottom: 20px;
  font-weight: bold;
  color: #333333;
}
.articles-msg{
  text-align: center;
  margin-bottom: 10px;
}
.articles-msg span{
  color: #666666;
  font-size: 14px;
  padding-left: 25px;
  padding-right: 25px;
}
.post_source{
  margin-top: 40px;
  margin-bottom: 25px;
}
.post_source span{
  color:red;
}
.articles-next{
  border-top: 1px solid #cfcfcf;
  padding-top: 22px;
  padding-bottom: 35px;
  font-size: 16px;
  color: #666;
}
.articles-next div{
  padding-left: 70%;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}
/*响应式代码*/
/*小屏情况下*/
@media (max-width: 768px){
.articles .articles-content img{
    width: 100% !important;
    height: 200px !important;
}
.articles-msg span{
    width: 100%;
    padding-left: 0px;
    padding-right: 0px;
    text-align: center;
    display: inline-block;
}
.articles-next div{
    padding-left: 0;
}
}



