
.question{
  background-color: #EEEEEE;
  min-height: 520px;
}
.ask-title{
  font-size: 24px;
  color: #000;
  font-family: "黑体";
  text-align: center;
  border-bottom: 1px solid #CCCCCC;
  line-height: 80px;
  margin-bottom: 25px;
}
.ask-box{
  margin-left: 7%;
  margin-top: 25px;
  margin-bottom: 25px;
}
.ask{
  font-size: 14px;
  color: #666666;
  margin-bottom: 20px;
  padding-left: 96px;
  position: relative;
  min-height: 40px;
  line-height: 40px;
}
.ask span{
  font-size: 24px;
  color: #333;
  display: inline-block;
  position: absolute;
  left: 5px;
}
.ask i{
  position: absolute;
  background-image: url('../../images/wen.png');
  background-size: 36px 40px;
  width: 36px;
  height: 40px;
  left: 40px;
}
.ask div{
  padding-top: 9px;
  line-height: 24px;
}
.answer{
  font-size: 14px;
  color: #666666;
  padding-left: 96px;
  position: relative;
  min-height: 40px;
}
.answer i{
  position: absolute;
  background-image: url('../../images/da.png');
  background-size: 36px 40px;
  width: 36px;
  height: 40px;
  left: 40px;
}
.answer div{
  padding-top: 9px;
  line-height: 24px;
}
.ask-btn{
  text-align: center;
  margin-top: 40px;
  margin-bottom: 70px;
}
.sq-btn{
  width: 300px;
  height: 50px;
  line-height: 50px;
  display: inline-block;
  background-color: #0B357C;
  color: #ffffff;
  border-radius: 8px;
  font-size: 26px;
  margin-top: 20px;
  margin-bottom: 40px;
}
.sq-btn:hover{
  color: #ffffff;
}
/*小屏情况下*/
@media (max-width: 992px) {
.ask-box{
    margin-left: 0px;
}
}
@media (max-width: 768px) {
.ask-btn{
    text-align: center;
    margin-top: 30px;
    margin-bottom: 40px;
}
.sq-btn{
    width: 200px;
    height: 50px;
    border-radius: 5px;
    font-size: 22px;
}
.ask-box{
    padding-left: 0;
    padding-right: 0;
}
.yema{
    margin-top: 40px;
}
.yema ul{
    float: left;
    padding-left: 0;
}
.question{
    min-height: 320px;
}
}
@media (min-width: 768px) {
.yema ul{
    float: right;
}
}



