
.listLi{
  background: orangered;
}
.head-title{
  background-color: #eeeeee;
}
.head-title .nav>li>a:focus,
.head-title .nav>li>a:hover{
  background-color: transparent;
}
.head-title .navbar-right{
  float: right;
}
.head-title .navbar-nav>li{
   float: left;
}
.head-title .navbar-nav li a{
  display: inline-block;
  color: #999999;
  font-size: 14px;
  padding: 8px 8px;
}
.head-title .navbar-nav li span{
  display: inline-block;
  color: #999999;
  line-height: 21px;
}
.head-title .navbar-nav li:nth-last-child(1) span{
  width: 0px;
  overflow: hidden;
}
.head-nav .navbar{
  margin-bottom: 0;
  border: none;
}
.head-nav .container{
  padding-left: 0px;
  padding-right: 0px;
}
.head-nav .navbar-header{
  margin-right: 0;
}
.head-nav .navbar-brand2{
  float: left;
  padding: 26px 15px;
  font-size: 18px;
  line-height: 20px;
}
.head-nav .navbar-toggle{
  margin-top: 34px;
  margin-bottom: 34px;
  margin-right: 32px;
  padding: 0;
  cursor: pointer;
  border: none;
}
.head-nav .navbar-toggle img{
  width: 42px;
}
.head-nav .navbar-default{
  background-color: #0B357C;
}
.head-nav .navbar-default .navbar-toggle:focus,
.head-nav .navbar-default .navbar-toggle:hover{
  background-color: transparent;
}
/*导航栏-每个li鼠标点击时的状态*/
.head-nav .navbar-default .navbar-nav .router-link-active{
  border-bottom: 4px solid #ffffff;
  background-color: transparent;
  color: #ffffff;
}
.head-nav .navbar-default .navbar-nav li a{
  color: #ffffff;
}
/*导航栏-下拉菜单每个li鼠标移入时的状态*/
.head-nav .navbar-default .navbar-nav li a:hover{
  color:#fff;
}
.head-nav .nav>li>a{
  font-size: 16px;
  color: #ffffff;
  text-align: center;
}

/*响应式代码*/
/*大屏情况下*/
@media (min-width: 768px) {
.head-nav .navbar-nav>li{
    margin-left: 28px;
}
.head-nav .navbar-nav>li>a{
    padding-top: 37px;
    padding-bottom: 37px;
}
}
/*小屏情况下*/
@media (max-width: 992px) {
.head-nav .navbar-brand2{
    display: none;
}
}
@media (max-width: 768px) {
.head-title .navbar-nav{
    margin: 0px -10px;
}
.head-nav .navbar-brand2{
    margin-left: 29px;
    padding-top: 20px;
    padding-bottom: 20px;
    display: block;
}
.head-nav .navbar-brand2 img{
    width: 120px;
}
.head-nav .navbar-toggle{
    margin-top: 22px;
    margin-bottom: 20px;
}
.head-nav .navbar-toggle img{
    width: 37px;
}
  /*导航栏-每个li鼠标点击时的状态*/
.head-nav .navbar-default .navbar-nav .router-link-active{
    border-bottom: none;
}
.container-fluid>.navbar-collapse, .container-fluid>.navbar-header, .container>.navbar-collapse, .container>.navbar-header{
    margin-right: 0!important;
    margin-left: 0!important;
}
}



