
.activity{
  background-color: #EEEEEE;
  min-height: 600px;
}
.activity-box{
  background-color: #fff;
  margin-top: 40px;
  margin-bottom: 40px;
}
.activity-title{
  font-size: 24px;
  color: #333;
  text-align: center;
  font-family: '黑体';
  line-height: 70px;
}
.activity-img img{
  width: 100%;
  max-height: 400px;
}
.yema{
  margin-bottom:40px;
}
/*响应式代码*/
/*小屏情况下*/
@media (max-width: 680px){
.activity{
    min-height: 300px;
}
.activity-title{
    font-size: 20px;
    line-height: 50px;
}
.yema{
    margin-top: 40px;
}
.yema ul{
    float: left;
    padding-left: 0;
}
}
@media (min-width: 768px) {
.yema{
    margin-top: 60px;
}
.yema ul{
    float: right;
}
}



